import React, { useState, useEffect } from "react";
import logo from "./logo.svg";
import "./App.css";

const App: React.FC = (props: any) => {
  const [posts, setPosts] = useState([]);
  useEffect(() => {
    fetch("http://localhost:8090/api/sample/getBirthStone", { method: "GET" })
      .then((res) => res.json())
      .then((data) => {
        setPosts(data);
      });
  }, []);

  return (
    <div className="App">
      <p>app</p>
      <div>
        <p>{posts}</p>
      </div>
    </div>
  );
};

export default App;
